<template>
  <div class="my-initiated-workflow-container">
    <div class="page-header">
      <div class="title">{{ projectObj.title }}</div>
      <div class="desc">{{ projectObj.desc }}</div>
    </div>
    <div class="page-content">
      <portal-form
        ref="searchFormRef"
        :searchBtnLoading="searchLoading"
        :form-config-list="formConfigList"
        :form-data="formData"
        :formRules="dateValidate"
        @reset-btn-click="resetSearch"
        @search-btn-click="search"
      ></portal-form>
      <div class="table-container">
        <table-page
          class="initiated-workflow-table"
          ref="workflowTableRef"
          :columns="columns"
          :sortArr="['startTime', 'endTime', 'status']"
          :getTableData="getTableData"
          :has-pagination="hasPagination"
          :pagination="paginationConfig"
          :is-search="isSearch"
        >
          <template v-if="detailBtnIsShow(scope.data.row)" slot-scope="scope" slot="operation">
            <el-button
              type="text"
              :disabled="!hasPerm('WF_MYTASK_OPERATORWORK_VIEW_BTN')"
              @click="handleDetail(scope.data.row)"
            >
              {{ $t('common.label.detail') }}
            </el-button>
          </template>
          <template slot-scope="scope" slot="taskStatus">
            <div class="flex flex-align">
              <span class="status-circle" :style="statusStyle(scope.data.row)"></span>
              <span>{{ scope.data.row.status }}</span>
            </div>
          </template>
        </table-page>
      </div>
    </div>
    <div class="page-dialog">
      <portal-dialog
        class="workflow-name-dialog"
        title="Select Workflow Name"
        ref="workflowNameDialogRef"
        :needSearchFormBottomLine="true"
        :dialogVisible.sync="workflowNameDialogVisible"
        :searchForm="workflowSearchForm"
        :dialogTable="dialogTable"
        :searchLoading.sync="workflowSearchLoading"
        @close-dialog="closeDialog"
        @search-reset="workflowNameDialogReset"
        @cancel-btn-click="closeDialog"
        @confirm-btn-click="confirmChange"
      ></portal-dialog>
    </div>
  </div>
</template>

<script>
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage';
import PortalDialog from '@/views/task-center/component/portal-dialog';
import { dateValidateRule } from '@/views/task-center/validator/index';
import {
  queryMyInitiatedTask,
  queryWorkFlowName,
  queryWorkFlowDataDict,
} from '@/views/task-center/api/api-task-center';
import dayjs from 'dayjs';

export default {
  name: 'myInitiatedWorkFlow',
  components: {
    TablePage,
    PortalForm,
    PortalDialog,
  },
  data() {
    return {
      projectObj: {
        title: this.$t('TAC.myInitiatedWorkFlow.title'),
        desc: this.$t('TAC.myInitiatedWorkFlow.desc'),
      },
      formData: {
        procDefId: '',
        workflowId: '',
        workflowName: '',
        workflowType: '',
        status: '',
        startTime: '',
      },
      dateValidate: dateValidateRule(),
      curSelectWorkflowNameToWorkflowType: '',
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          this.selectDate = minDate.getTime();
          if (maxDate) {
            this.selectDate = '';
          }
        },
        disabledDate: (time) => {
          if (this.selectDate !== '') {
            const one = 30 * 24 * 3600 * 1000;
            const minTime = this.selectDate - one;
            const maxTime = this.selectDate + one;
            return time.getTime() < minTime || time.getTime() > maxTime;
          }
        },
      },
      searchLoading: false,
      searchFormWFT: [{ value: '', label: 'All' }],
      searchFormStatus: [{ value: '', label: 'All' }],
      columns: [
        {
          label: this.$t('TAC.myInitiatedWorkFlow.workflowId'),
          prop: 'workflowId',
          align: 'left',
          'min-width': '122px',
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.workflowName'),
          prop: 'workflowName',
          align: 'left',
          'min-width': '150px',
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.workflowType'),
          prop: 'workflowType',
          align: 'left',
          'min-width': '150px',
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.startTime'),
          prop: 'startTime',
          align: 'left',
          'min-width': '179px',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.endTime'),
          prop: 'endTime',
          align: 'left',
          'min-width': '173px',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.currentTask'),
          prop: 'currentTask',
          align: 'left',
          width: '200px',
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.currentTaskOwner'),
          prop: 'currentTaskOwner',
          align: 'left',
          'min-width': '180px',
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.status'),
          prop: 'status',
          align: 'left',
          __slotName: 'taskStatus',
          'min-width': '150px',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          'min-width': '100px',
          fixed: 'right',
        },
      ],
      hasPagination: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      isSearch: false,
      workflowNameDialogVisible: false,
      dialogTable: {
        columns: [
          {
            label: this.$t('TAC.myInitiatedWorkFlow.workflowName'),
            prop: 'workflowName',
            align: 'left',
          },
          {
            label: this.$t('TAC.myInitiatedWorkFlow.workflowType'),
            prop: 'workflowType',
            align: 'left',
          },
        ],
        isSearch: false,
        hasPagination: true,
        isSingleCheck: true,
        paginationConfig: {
          total: 0,
          pageIndex: 1,
          pageSize: 5,
        },
        getTableData: this.getDialogTableData,
        singleChange: this.selectChange,
      },
      selectTableData: {},
      workflowSearchLoading: false,
      workflowSearchFormData: {
        workflowName: '',
        workflowType: '',
      },
    };
  },
  computed: {
    formConfigList() {
      return [
        {
          label: this.$t('TAC.myInitiatedWorkFlow.workflowId'),
          key: 'workflowId',
          prop: 'workflowId',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          formItemClass: 'org-short-code',
          label: this.$t('TAC.myInitiatedWorkFlow.workflowName'),
          key: 'workflowName',
          prop: 'workflowName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          slots: [
            {
              comp: 'el-link',
              name: 'append',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.workflowNameDialogVisible = true;
                  this.workflowSearchForm.formData.workflowType = this.searchFormWFT[0].value;
                  this.dialogTable.isSearch = true;
                },
              },
            },
          ],
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.workflowType'),
          key: 'workflowType',
          prop: 'workflowType',
          comp: 'el-select',
          options: this.searchFormWFT,
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.status'),
          key: 'status',
          prop: 'status',
          comp: 'el-select',
          options: this.searchFormStatus,
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('TAC.myInitiatedWorkFlow.startTime'),
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          prop: 'startTime',
          comp: [
            {
              key: 'startTime',
              comp: 'el-date-picker',
              bind: {
                placeholder: this.$t('common.placeholder.enter'),
                align: 'right',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'picker-options': this.pickerOptions,
                'range-separator': '~',
                'value-format': 'yyyy-MM-dd HH:mm:ss',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Please select',
                'end-placeholder': 'Please select',
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
      ];
    },
    workflowSearchForm() {
      return {
        formConfigList: [
          {
            label: this.$t('TAC.myInitiatedWorkFlow.workflowName'),
            key: 'workflowName',
            prop: 'workflowName',
            comp: 'el-input',
            bind: {
              clearable: true,
              placeholder: this.$t('common.placeholder.enter'),
            },
          },
          {
            label: this.$t('TAC.myInitiatedWorkFlow.workflowType'),
            key: 'workflowType',
            prop: 'workflowType',
            comp: 'el-select',
            options: this.searchFormWFT,
            bind: {
              placeholder: this.$t('common.placeholder.select'),
            },
          },
        ],
        formData: this.workflowSearchFormData,
      };
    },
  },
  watch: {
    formData: {
      handler(val) {
        if (val.workflowType === this.curSelectWorkflowNameToWorkflowType) return;
        this.formData.workflowName = '';
        this.formData.procDefId = '';
      },
      deep: true,
    },
  },
  created() {
    this.formData.startTime = [
      dayjs(
        new Date(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 0, 0, 0).getTime() - 1000 * 3600 * 24 * 7)
      ).format('YYYY-MM-DD HH:mm:ss'),
      dayjs(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 23, 59, 59)).format('YYYY-MM-DD HH:mm:ss'),
    ];
    queryWorkFlowDataDict({
      dictCodes: ['CPS.TASK.TYPE', 'CPS.WORKFLOW.ORDER.STATUS'],
    }).then((res) => {
      if (res.header && res.header.success) {
        this.searchFormWFT = res.dictDetailsMap['CPS.TASK.TYPE'];
        this.searchFormStatus = res.dictDetailsMap['CPS.WORKFLOW.ORDER.STATUS'];
        if (this.searchFormStatus[0].label !== 'All') {
          this.searchFormStatus.unshift({ label: 'All', value: '' });
        }
      }
    });
  },
  methods: {
    statusStyle(row) {
      switch (row.status) {
        case 'Pending':
          return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
        case 'Suspended':
          return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
        case 'Completed':
          return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
        default:
          break;
      }
    },
    resetSearch() {
      this.formData.procDefId = '';
      this.$refs.searchFormRef.resetFields();
      this.isSearch = true;
    },
    detailBtnIsShow(val) {
      return val.viewFlag;
    },
    search() {
      this.$refs.searchFormRef.validate((result) => {
        if (result) {
          this.searchLoading = true;
          this.isSearch = true;
        }
      });
    },
    handleDetail(val) {
      this.openNewWindow({ path: 'workflowDetail', query: val });
    },
    currentTaskIsShow(val) {
      return val.procStatus === 'Pending' || val.procStatus === 'Suspended';
    },
    currentOwnerIsShow(val) {
      return val.procStatus === 'Pending' || val.procStatus === 'Suspended';
    },
    getTableData() {
      return new Promise((resolve) => {
        let wkfType = '';
        if (this.formData.workflowType.length > 1) {
          this.searchFormWFT.some((item) => {
            if (item.label === this.formData.workflowType) {
              wkfType = item.value;
              return true;
            }
          });
        } else {
          wkfType = this.formData.workflowType;
        }
        let params = {
          procDefId: this.formData.procDefId,
          processId: this.formData.workflowId,
          procName: this.formData.workflowName,
          procType: wkfType,
          workflowName: this.formData.workflowName,
          createTimeFrom: this.formData.startTime[0],
          createTimeTo: this.formData.startTime[1],
          procStatus: this.formData.status,
          pageNo: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
        };
        queryMyInitiatedTask({ processVO: params }).then((res) => {
          this.isSearch = false;
          this.searchLoading = false;
          if (res.header && res.header.success) {
            if (res.processDTOList.length > 0) {
              this.hasPagination = true;
              let dataList = res.processDTOList.map((item) => {
                let currentTask = item.currentTask && this.currentTaskIsShow(item) ? item.currentTask.taskName : '-';
                let taskId = item.currentTask ? item.currentTask.taskId : '-';
                let currentTaskOwner =
                  item.currentTask && this.currentOwnerIsShow(item) ? item.currentTask.ownerName : '-';
                let workflowType = item.procType;
                if (this.searchFormWFT.length > 2) {
                  this.searchFormWFT.some((wktItem) => {
                    if (wktItem.value === item.procType) {
                      workflowType = wktItem.label;
                      return true;
                    }
                  });
                }
                let time = item.createTime.split(' ');
                let date = time[0].split('-');
                let startTime = date[2] + '/' + date[1] + '/' + date[0] + ' ' + time[1];
                let endTime = '-';
                if (item.endTime) {
                  let expireTime = item.endTime.split(' ');
                  let expDate = expireTime[0].split('-');
                  endTime = expDate[2] + '/' + expDate[1] + '/' + expDate[0] + ' ' + expireTime[1];
                }
                let viewFlag = true;
                !(item.currentTask && item.currentTask.viewFlag) ? (viewFlag = false) : '';
                return {
                  workflowId: item.processId,
                  workflowName: item.procName,
                  workflowType,
                  startTime,
                  endTime,
                  currentTask,
                  taskId,
                  currentTaskOwner,
                  status: item.procStatus,
                  viewFlag,
                };
              });
              resolve({
                header: res.header,
                list: dataList,
                totalRecords: res.count,
              });
            } else {
              resolve({
                header: res.header,
                list: [],
              });
            }
          } else {
            resolve(res);
            this.isSearch = false;
          }
        });
      });
    },
    getDialogTableData() {
      return new Promise((resolve) => {
        let workflowTypeCondition = '';
        let workflowTypeCodetoLabel = '';
        this.searchFormWFT.some((item) => {
          if (item.value === this.workflowSearchFormData.workflowType) {
            workflowTypeCodetoLabel = item.label;
            return true;
          }
        });
        if (workflowTypeCodetoLabel) {
          if (workflowTypeCodetoLabel !== 'All') {
            if (workflowTypeCodetoLabel === 'Transaction and Action') {
              workflowTypeCondition = 'TRANSATION_AND_ACTION';
            } else {
              workflowTypeCondition = workflowTypeCodetoLabel.toUpperCase().split(' ').join('_');
            }
          }
        }
        queryWorkFlowName({
          workflowNameCondition: this.workflowSearchFormData.workflowName,
          workflowTypeCondition,
          pageSize: this.dialogTable.paginationConfig.pageSize,
          pageIndex: this.dialogTable.paginationConfig.pageIndex,
        }).then((res) => {
          this.workflowSearchLoading = false;
          this.dialogTable.isSearch = false;
          if (res.header && res.header.success) {
            if (res.workflowNameList && res.workflowNameList.length > 0) {
              let list = res.workflowNameList.slice(
                (this.dialogTable.paginationConfig.pageIndex - 1) * this.dialogTable.paginationConfig.pageSize,
                this.dialogTable.paginationConfig.pageIndex * this.dialogTable.paginationConfig.pageSize
              );
              list.forEach((item, idx) => {
                list[idx].workflowType = item.category;
              });
              resolve({
                header: res.header,
                list,
                totalRecords: res.workflowNameList.length,
              });
            } else {
              resolve({ header: { success: true }, list: [] });
            }
          }
        });
      });
    },
    closeDialog() {
      this.workflowNameDialogVisible = false;
    },
    workflowNameDialogReset() {
      this.$refs.workflowNameDialogRef.$refs.dialogSearchForm.resetFields();
      this.dialogTable.isSearch = true;
    },
    selectChange(val) {
      this.selectTableData = val;
    },
    confirmChange() {
      if (Object.keys(this.selectTableData).length === 0) {
        this.$modal.msgError('Please select at least one.');
        return;
      }
      this.formData.procDefId = this.selectTableData.procDefId;
      this.formData.workflowName = this.selectTableData.workflowName;
      if (this.selectTableData.workflowType === 'TRANSATION_AND_ACTION') {
        this.formData.workflowType = 'Transaction and Action';
        this.curSelectWorkflowNameToWorkflowType = 'Transaction and Action';
      } else {
        if (this.selectTableData.workflowType) {
          let workflowType = this.selectTableData.workflowType
            .toLocaleLowerCase()
            .split('_')
            .map((item) => {
              return item.replace(item[0], item[0].toLocaleUpperCase());
            });
          this.formData.workflowType = workflowType.join(' ');
          this.curSelectWorkflowNameToWorkflowType = workflowType.join(' ');
        }
      }
      this.workflowNameDialogVisible = false;
    },
  },
};
</script>

