  padding: 20px;
  background-color: $base-menu-background;
  .title {
    height: 32px;
    font-family: Huawei Sans;
    font-weight: 700;
    color: $--text_color--emphasis;
    font-size: 24px;
    margin-bottom: 8px;
  }
  .desc {
    width: 1092px;
    height: 16px;
    font-family: Huawei Sans;
    color: $--text_color--secondary;
    font-size: 12px;
  }
}
.page-content {
  margin-top: 1px;
  /deep/ .org-short-code {
    position: relative;
  }
  .table-container {
    background-color: $base-menu-background;
    padding: 20px 20px 24px 20px;
  }
}
/deep/ .workflow-name-dialog {
  .el-divider--horizontal {
    display: none;
  }
  .pagination-region {
    .el-select--mini {
      display: none;
    }
    .pagination__total {
      margin-right: 18px;
    }
    .show-title {
      display: none;
    }
    .go-to-buttom {
      display: none;
    }
    .el-pagination__jump {
      display: none;
    }
  }
}
</style>
